<template>
  <el-dialog
    class="customDialog"
    :model-value="visible"
    :width="props.width"
    @open="open"
    @close="close"
  >
    <template #header>
      <header>{{ props.title }}</header>
    </template>
    <slot name="default"></slot>
    <template #footer>
      <slot name="footer"></slot>
    </template>
  </el-dialog>
</template>

<script lang="ts" setup>
import { ref } from "vue";

const props = withDefaults(
  defineProps<{
    title: string;
    width?: string | number;
    open?: () => void;
    close?: () => void;
  }>(),
  {
    width: 500,
  }
);

const visible = ref(false);
const open = () => {
  props.open && props.open();
};
const close = () => {
  visible.value = false;
  props.close && props.close();
};
const toggle = (value: boolean) => {
  visible.value = value;
  return Promise.resolve({
    visible: visible.value,
  });
};
defineExpose({
  toggle,
});
</script>
<style lang="scss">
.el-dialog.customDialog {
  border-radius: 8px;

  .el-dialog__header {
    padding: 4px 0 16px;
    border-bottom: 1px solid #edf1f6;
  }

  header {
    color: #303133;
    font-size: 18px;
    line-height: 1;
  }

  .el-dialog__body {
    padding: 20px;
  }
}
</style>
